<template>
	<view class="navbar">
		<view class="navbar-fixed" :style="{backgroundColor: navColor}">
			<view :style="{height:statusBarHeight+'px'}"></view>
			<view v-if="pickerList.length>0" class="navbar-content" :style="{height:navBarHeight+'px',width:windowWidth+'px'}">
				<view>
					<picker @change="bindPickerChange" :value="pickerIndex" :range="pickerList" range-key="title">
						<view style="color: #FFFFFF;">{{pickerList[pickerIndex].title}}</view>
					</picker>
				</view>
			</view>
		</view>
		<view :style="{height: statusBarHeight+navBarHeight+'px'}"></view>
	</view>
</template>

<script>
	export default {
		props: {
			pickerList: {
			  type: Array,
			  default() {
			    return [];
			  }
			},
			navColor: String
		},
		data() {
			return {
				statusBarHeight: 20,
				navBarHeight: 44,
				windowWidth: 375,
				val: '',
				pickerIndex: 0
			};
		},
		watch:{
			value(newVal){
				this.val = newVal
			}
		},
		created() {
			const info = uni.getSystemInfoSync()
			this.statusBarHeight = info.statusBarHeight
			this.windowWidth = info.windowWidth
			// #ifndef H5 || APP-PLUS || MP-ALIPAY
			//微信胶囊位置
			const menuButtonInfo = uni.getMenuButtonBoundingClientRect()
			this.navBarHeight = (menuButtonInfo.bottom - info.statusBarHeight) + (menuButtonInfo.top - info.statusBarHeight) + 4
			// #endif
		},
		methods: {
			bindPickerChange: function(e) {
				this.pickerIndex = e.detail.value
				this.$emit('pickerSwitch', this.pickerList[e.detail.value].platform)
			}
		}
	}
</script>

<style lang="scss">

	.navbar {
		.navbar-fixed {
			position: fixed;
			top: 0;
			left: 0;
			z-index: 99;
			width: 100%;
			background-color: $anquanke-back-color;

			.navbar-content {
				display: flex;
				justify-content: center;
				align-items: center;
				padding: 0 60upx;
				height: 90upx;
				box-sizing: border-box;
				font-size: 40upx;
			}
		}

	}
</style>
